<template>
  <div class="row valign-wrapper">
    <div class="input-field col s12">
      <select ref="monthSelect" v-model.number="transactionsMonthFilter">
        <option selected value="12">Показать все операции</option>
        <option v-for="(month, index) in monthsArray" :key="index" :value="index">{{ month }}</option>
      </select>
      <label>Фильтрация по месяцам</label>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { onMounted, ref } from 'vue';
import { useTransactionsStore } from '@/stores/transactions';
import { storeToRefs } from 'pinia';

const { transactionsMonthFilter } = storeToRefs(useTransactionsStore());

const monthSelect = ref();
const monthSelectInstance = ref();

const monthsArray = [ 'Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь' ];

onMounted(() => {
  monthSelectInstance.value = M.FormSelect.init(monthSelect.value);
});
</script>